	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog de David A. Lareo","subtitle":"Sitio web personal: artículos, proyectos y material de autobombo","copyright":"GNU Affero General Public License","menu":[{"label":"Artículos","path":"/"},{"label":"Proyectos","path":"/projects/"},{"label":"Sobre mí","path":"/about/"}],"author":{"name":"David A. Lareo","email":"dalareo@gmail.com","telegram":"dalareo","twitter":"dalareo","github":"dalareo","rss":"https://dalareo.github.io/rss.xml","vk":"#"}}},"markdownRemark":{"id":"/home/usuario/Software/dalareo.github.io/src/pages/pages/2018-05-17---about/index.md absPath of file >>> MarkdownRemark","html":"<p>Soy indisciplinado y excesivo. Me formé en los límites de la academia y reivindico el papel de lo <strong>mestizo</strong> y de las <strong>analogías</strong> como herramienta para conocer el mundo. A veces escribo <a href=\"https://dalareo.wordpress.com/\"><strong>poemas</strong></a> y hago música. Soy demasiado práctico para la filosofía académica y demasiado crítico para la ingeniería informática. Casi todo lo que sé lo aprendí equivocándome, como muestra mi <a href=\"https://www.dropbox.com/s/0n5uaauwace9w2s/CV_DavidALareo.pdf?dl=0\"><del>curriculum vitae</del></a>. Diseño productos y servicios que respeten los valores fundamentales de la <a href=\"https://es.wikipedia.org/wiki/%C3%89tica_hacker\"><strong>ética hacker</strong></a>. Defiendo la <strong>cultura libre</strong> y la <strong>producción abierta del conocimiento</strong>.</p>\n<p>Me apasionan muchas cosas y por eso tengo la mochila cargada de historias, algunas de ellas son: <strong>educación</strong>, <strong>emprendimiento social</strong>, <strong>economía del bien común</strong>, <strong>innovación social</strong>, <strong>desarrollo web</strong>, <strong>administración de infraestructura digital</strong>, <strong>diseño y gestión ágil</strong>…y las que faltan!</p>\n<p>Dirijo <a href=\"https://fricolab.github.io\">FricolaB</a> y escribo el código y los versos en <a href=\"https://educaas.github.io\">EduCaaS</a>.</p>\n<p>Aquí abajo estoy experimentando con una visualización de los principales hitos de mi recorrido profesional y académico. Por lo de pronto hay que echarle bastante imaginación pero puede ser útil para ver las <strong>distintas etapas de mi carrera</strong>. <strong>Coloca el puntero del ratón sobre un nodo</strong> para ver el nombre de ese elemento y <strong>arrastra los nodos</strong> para comprobar las relaciones entre ellos y el peso relativo en mi curriculum.</p>\n<iframe src=\"https://dalareo.github.io/curriculum/index.html\" width=100% height=360 marginwidth=\"0\" marginheight=\"0\" scrolling=\"no\"></iframe>","frontmatter":{"title":"Sobre mí","date":null,"description":"Un poco de contexto sobre las cosas que he hecho y las que me gustaría hacer. Lo cuento con un gráfico porque es demasiado complejo para un CV."}}},"pathContext":{"slug":"/about"}}

